body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: #f1f1f1;
}
.featured-image {
  color: #fff;
  text-align: center;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  display: table;
  width: 100%;
  height: 300px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.6)), to(rgba(0, 0, 0, 0.6))), url('/static/images/home/featured-2.jpg');
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('/static/images/home/featured-2.jpg');
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('/static/images/home/featured-2.jpg');
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('/static/images/home/featured-2.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.featured-image .featured-content {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  text-align: center;
}
.featured-image .featured-content .featured-content-gradient{
  padding-top: 20px;
  padding-bottom: 20px;
  background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%,rgba(255,255,255,0.25) 20%,rgba(255,255,255,0.25) 80%,rgba(255,255,255,0) 100%);
  background: -webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0)),color-stop(20%, rgba(255,255,255,0.25)),color-stop(80%, rgba(255,255,255,0.25)),to(rgba(255,255,255,0)));
  background: -o-linear-gradient(left, rgba(255,255,255,0) 0%,rgba(255,255,255,0.25) 20%,rgba(255,255,255,0.25) 80%,rgba(255,255,255,0) 100%);
  background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,0.25) 20%,rgba(255,255,255,0.25) 80%,rgba(255,255,255,0) 100%);
}
.featured-image .featured-title {
  font-size: 48px;
  text-transform: uppercase;
}

@media ( min-width: 992px ) {
  .featured-image {
    height: 600px;
  }
}


